import Canvas from '../helpers/canvas';
import {
  MATRIX_SIZES,
  DEFAULT_MAIN_COLOR,
  DEFAULT_SECOND_COLOR,
  TOOL_IDS,
} from '../constants';
import { actionTypes } from './actions';

export const initialState = {
  mainColor: DEFAULT_MAIN_COLOR,
  secondColor: DEFAULT_SECOND_COLOR,
  matrixSize: MATRIX_SIZES['32x32'],
  canvasList: [new Canvas(MATRIX_SIZES['32x32']).createMatrix()],
  reDrawFlag: false,
  activeCanvasIndex: 0,
  activePixel: null,
  activeToolId: TOOL_IDS.PEN,
  mouseDownPixel: null,
  mouseUpPixel: null,
  fps: 12,
  user: null,
};

export const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_MAIN_COLOR: {
      const activeCanvas = state.canvasList[state.activeCanvasIndex];
      activeCanvas.setMainColor(action.color);

      return {
        ...state,
        mainColor: action.color,
      };
    }

    case actionTypes.SET_SECOND_COLOR: {
      const activeCanvas = state.canvasList[state.activeCanvasIndex];
      activeCanvas.setSecondColor(action.color);

      return {
        ...state,
        secondColor: action.color,
      };
    }

    case actionTypes.SWAP_COLOR: {
      const mainColor = state.secondColor;
      const secondColor = state.mainColor;

      const activeCanvas = state.canvasList[state.activeCanvasIndex];
      activeCanvas.setMainColor(mainColor);
      activeCanvas.setSecondColor(secondColor);

      return {
        ...state,
        mainColor,
        secondColor,
      };
    }

    case actionTypes.SET_ACTIVE_PIXEL: {
      const activeCanvas = state.canvasList[state.activeCanvasIndex];
      activeCanvas.setActivePixel(action.activePixel);
      return {
        ...state,
        activePixel: action.activePixel,
        reDrawFlag: !state.reDrawFlag,
      };
    }

    case actionTypes.REMOVE_ACTIVE_PIXEL: {
      const activeCanvas = state.canvasList[state.activeCanvasIndex];
      activeCanvas.resetTemporaryMatrix();

      return {
        ...state,
        activePixel: null,
        reDrawFlag: !state.reDrawFlag,
      };
    }

    case actionTypes.SET_ACTIVE_TOOL_ID: {
      const activeCanvas = state.canvasList[state.activeCanvasIndex];
      activeCanvas.setActiveToolId(action.activeToolId);

      return {
        ...state,
        activeToolId: action.activeToolId,
      };
    }

    case actionTypes.SET_MOUSE_DOWN_PIXEL: {
      const activeCanvas = state.canvasList[state.activeCanvasIndex];
      activeCanvas.setMouseDownPixel(action.mouseDownPixel);

      return {
        ...state,
        mouseDownPixel: action.mouseDownPixel,
        reDrawFlag: !state.reDrawFlag,
      };
    }

    case actionTypes.SET_MOUSE_UP_PIXEL: {
      const activeCanvas = state.canvasList[state.activeCanvasIndex];
      activeCanvas.setMouseUpPixel(action.mouseUpPixel);

      return {
        ...state,
        mouseDownPixel: null,
        mouseUpPixel: action.mouseUpPixel,
        reDrawFlag: !state.reDrawFlag,
      };
    }

    case actionTypes.UPDATE_CANVAS_LIST: {
      return {
        ...state,
        canvasList: action.canvasList,
      };
    }

    case actionTypes.SET_ACTIVE_CANVAS_INDEX: {
      const activeCanvas = state.canvasList[action.activeCanvasIndex];
      activeCanvas.setMainColor(state.mainColor);
      activeCanvas.setSecondColor(state.secondColor);
      activeCanvas.setActiveToolId(state.activeToolId);
      return {
        ...state,
        activeCanvasIndex: action.activeCanvasIndex,
      };
    }

    case actionTypes.ADD_CANVAS: {
      const newCanvas = new Canvas(state.matrixSize).createMatrix();
      newCanvas.setMainColor(state.mainColor);
      newCanvas.setSecondColor(state.secondColor);
      newCanvas.setActiveToolId(state.activeToolId);

      const canvasList = [...state.canvasList, newCanvas];

      return {
        ...state,
        canvasList,
        activeCanvasIndex: canvasList.length - 1,
      };
    }

    case actionTypes.DELETE_CANVAS: {
      const canvasList = [...state.canvasList];
      canvasList.splice(action.index, 1);

      const activeCanvasIndex = canvasList.length - 1;
      const activeCanvas = canvasList[activeCanvasIndex];
      activeCanvas.setMainColor(state.mainColor);
      activeCanvas.setSecondColor(state.secondColor);
      activeCanvas.setActiveToolId(state.activeToolId);

      return {
        ...state,
        canvasList,
        activeCanvasIndex,
      };
    }

    case actionTypes.COPY_CANVAS: {
      const copyCanvas = state.canvasList[action.index];
      const canvasList = [...state.canvasList, copyCanvas.getCopy()];

      const activeCanvasIndex = canvasList.length - 1;
      const activeCanvas = canvasList[activeCanvasIndex];
      activeCanvas.setMainColor(state.mainColor);
      activeCanvas.setSecondColor(state.secondColor);
      activeCanvas.setActiveToolId(state.activeToolId);

      return {
        ...state,
        canvasList,
        activeCanvasIndex,
      };
    }

    case actionTypes.SET_FPS: {
      return {
        ...state,
        fps: action.fps,
      };
    }

    case actionTypes.SET_USER: {
      return {
        ...state,
        user: action.user,
      };
    }

    case actionTypes.DELETE_USER: {
      return {
        ...state,
        user: null,
      };
    }

    case actionTypes.SET_MATRIX_SIZE: {
      state.canvasList.forEach((canvas) => {
        canvas.setMatrixSize(action.matrixSize);
      });

      return {
        ...state,
        matrixSize: action.matrixSize,
        reDrawFlag: !state.reDrawFlag,
      };
    }

    default:
      return state;
  }
};
