import map from 'lodash/map';
import get from 'lodash/get';

import {
  DEFAULT_MAIN_COLOR, DEFAULT_SECOND_COLOR, ACTIVE_COLOR, TOOL_IDS, INITIAL_COLOR,
} from '../../constants';
import Pixel from './pixel';

export default class Canvas {
  constructor(matrixSize) {
    this.matrixSize = matrixSize;
    this.matrix = null;
    this.temporaryMatrix = null;
    this.reDrawFlag = false;

    this.mainColor = DEFAULT_MAIN_COLOR;
    this.secondColor = DEFAULT_SECOND_COLOR;
    this.activeToolId = TOOL_IDS.PEN;
    this.activePixel = null;
    this.mouseDownPixel = null;
    this.mouseUpPixel = null;
  }

  createMatrix() {
    const arr = Array(this.matrixSize);
    this.matrix = map(arr, () => map(arr, () => new Pixel()));
    this.temporaryMatrix = map(arr, () => map(arr, () => new Pixel()));
    return this;
  }

  getCopy() {
    const copy = new Canvas(this.matrixSize).createMatrix();
    copy.matrix.forEach((row, indexRow) => {
      row.forEach((pixel, indexColumn) => {
        pixel.setColor(this.matrix[indexRow][indexColumn].color);
      });
    });

    return copy;
  }

  setMainColor(color) {
    this.mainColor = color;
  }

  setSecondColor(color) {
    this.secondColor = color;
  }

  setActiveToolId(activeToolId) {
    this.activeToolId = activeToolId;
  }

  setMatrixSize(matrixSize) {
    this.matrixSize = matrixSize;
    const arr = Array(this.matrixSize);
    this.temporaryMatrix = map(arr, () => map(arr, () => new Pixel()));

    const matrix = map(arr, () => map(arr, () => new Pixel()));
    matrix.forEach((row, indexRow) => {
      row.forEach((pixel, indexColumn) => {
        const oldPixel = get(this.matrix, [indexRow, indexColumn]);
        if (oldPixel) {
          pixel.setColor(oldPixel.color);
        }
      });
    });
    this.matrix = matrix;
    this.reDrawFlag = !this.reDrawFlag;
  }

  setActivePixel(activePixel) {
    this.resetTemporaryMatrix();

    if (this.mouseDownPixel) {
      switch (this.activeToolId) {
        case TOOL_IDS.STROKE: {
          this.draw(this.mouseDownPixel, activePixel, true);
          break;
        }

        case TOOL_IDS.PEN:
        case TOOL_IDS.ERASER:
        case TOOL_IDS.DITHERING: {
          this.draw(this.activePixel, activePixel, false);
          break;
        }

        default: {
          break;
        }
      }
    } else {
      this.setActiveColor(activePixel);
    }

    this.activePixel = activePixel;
    this.reDrawFlag = !this.reDrawFlag;
  }

  setActiveColor(activePixel) {
    if (activePixel && this.isActivePixelIntoCanvas(activePixel)) {
      const { indexRow, indexColumn } = activePixel;
      const activePixelNode = this.temporaryMatrix[indexRow][indexColumn];
      activePixelNode.setColor(ACTIVE_COLOR);
    }
  }

  unSetActiveColor(activePixel) {
    if (activePixel && this.isActivePixelIntoCanvas(activePixel)) {
      const { indexRow, indexColumn } = activePixel;
      const activePixelNode = this.temporaryMatrix[indexRow][indexColumn];
      activePixelNode.setColor(INITIAL_COLOR);
    }
  }

  resetTemporaryMatrix() {
    const arr = Array(this.matrixSize);
    this.temporaryMatrix = map(arr, () => map(arr, () => new Pixel()));
  }

  isActivePixelIntoCanvas({ indexRow, indexColumn }) {
    return indexRow < this.matrixSize && indexRow >= 0 && indexColumn < this.matrixSize && indexColumn >= 0;
  }

  setMouseDownPixel(mouseDownPixel) {
    if (mouseDownPixel && this.isActivePixelIntoCanvas(mouseDownPixel)) {
      this.mouseDownPixel = mouseDownPixel;

      switch (this.activeToolId) {
        case TOOL_IDS.PEN:
        case TOOL_IDS.ERASER:
        case TOOL_IDS.DITHERING: {
          this.draw(this.mouseDownPixel, this.mouseDownPixel, false);
          this.unSetActiveColor(mouseDownPixel);
          break;
        }

        case TOOL_IDS.STROKE: {
          this.draw(this.mouseDownPixel, this.mouseDownPixel, true);
          break;
        }

        case TOOL_IDS.BUCKET_SC: {
          this.draw(this.mouseDownPixel, this.mouseDownPixel, false);
          break;
        }

        case TOOL_IDS.BUCKET: {
          this.bucket(this.mouseDownPixel);
          break;
        }

        default: {
          break;
        }
      }

      this.reDrawFlag = !this.reDrawFlag;
    }
  }

  setMouseUpPixel(mouseUpPixel) {
    this.mouseUpPixel = mouseUpPixel;

    switch (this.activeToolId) {
      case TOOL_IDS.STROKE: {
        this.draw(this.mouseDownPixel, mouseUpPixel, false);
        break;
      }

      default: {
        break;
      }
    }

    this.mouseDownPixel = null;
    this.setActiveColor(this.mouseUpPixel);
    this.reDrawFlag = !this.reDrawFlag;
  }

  draw(firstPixel, secondPixel, isTemporary) {
    if (firstPixel && secondPixel && this.activeToolId) {
      switch (this.activeToolId) {
        case TOOL_IDS.STROKE:
        case TOOL_IDS.PEN:
        case TOOL_IDS.ERASER:
        case TOOL_IDS.DITHERING: {
          this.drawLine(firstPixel, secondPixel, isTemporary);
          break;
        }

        case TOOL_IDS.BUCKET_SC: {
          const { indexRow, indexColumn } = firstPixel;
          const { color } = this.matrix[indexRow][indexColumn];

          this.matrix.forEach((row) => {
            row.forEach((pixel) => {
              if (pixel.color === color) {
                pixel.setColor(this.mainColor);
              }
            });
          });
          break;
        }

        default:
          break;
      }
    }
  }

  drawLine = (
    { indexRow: x1, indexColumn: y1 },
    { indexRow: x2, indexColumn: y2 },
    isTemporary,
  ) => {
    const deltaX = Math.abs(x2 - x1);
    const deltaY = Math.abs(y2 - y1);
    const signX = (x1 < x2) ? 1 : -1;
    const signY = (y1 < y2) ? 1 : -1;

    let error = deltaX - deltaY;
    let x = x1;
    let y = y1;

    this.changePixelColor(x2, y2, isTemporary);

    while (x !== x2 || y !== y2) {
      this.changePixelColor(x, y, isTemporary);
      const error2 = error * 2;

      if (error2 > -deltaY) {
        error -= deltaY;
        x += signX;
      }
      if (error2 < deltaX) {
        error += deltaX;
        y += signY;
      }
    }
  }

  bucket = ({ indexRow, indexColumn }) => {
    const pixelStack = [[indexColumn, indexRow]];
    const { color: initColor } = this.matrix[indexRow][indexColumn];

    if (this.mainColor === initColor) {
      return;
    }

    while (pixelStack.length) {
      const newPos = pixelStack.pop();
      const x = newPos[0];
      let y = newPos[1];

      while (y !== 0 && initColor === this.matrix[y - 1][x].color) {
        y -= 1;
      }

      let leftFlag = true;
      let rightFlag = true;

      do {
        this.changePixelColor(y, x, false);
        if (x - 1 >= 0 && initColor === this.matrix[y][x - 1].color) {
          if (leftFlag) {
            pixelStack.push([x - 1, y]);
            leftFlag = false;
          }
        } else {
          leftFlag = true;
        }
        if (x + 1 < this.matrixSize && initColor === this.matrix[y][x + 1].color) {
          if (rightFlag) {
            pixelStack.push([x + 1, y]);
            rightFlag = false;
          }
        } else {
          rightFlag = true;
        }
        y += 1;
      } while (y < this.matrixSize && initColor === this.matrix[y][x].color);
    }
  }

  getColorByIndexes(indexRow, indexColumn) {
    switch (this.activeToolId) {
      case TOOL_IDS.ERASER: {
        return INITIAL_COLOR;
      }

      case TOOL_IDS.DITHERING: {
        const sum = indexRow + indexColumn;
        return sum % 2 === 0 ? this.mainColor : this.secondColor;
      }

      default:
        return this.mainColor;
    }
  }

  changePixelColor(indexRow, indexColumn, isTemporary) {
    if (this.isActivePixelIntoCanvas({ indexRow, indexColumn })) {
      const matrix = isTemporary ? this.temporaryMatrix : this.matrix;
      const pixelNode = matrix[indexRow][indexColumn];
      pixelNode.setColor(this.getColorByIndexes(indexRow, indexColumn));
    }
  }
}


